import React from 'react';

type DiffPropsType = {
    x: number
    y: number
    devColumnSum?: number
    testColumnSum: number | undefined
    prodColumnSum?: number
}

export const DiffComponent = (props: DiffPropsType) => {
    debugger
    let diff = ''
    if (props.devColumnSum && props.testColumnSum) {
        if (props.devColumnSum > props.testColumnSum) {
            diff = `-${props.devColumnSum - props.testColumnSum}`
        }
        if (props.devColumnSum < props.testColumnSum) {
            diff = `${props.testColumnSum - props.devColumnSum}`
        }
        if (props.devColumnSum === props.testColumnSum) {
            diff = '0';
        }
    }
    if (props.prodColumnSum && props.testColumnSum) {
        if (props.testColumnSum > props.prodColumnSum) {
            diff = `-${props.testColumnSum - props.prodColumnSum}`
        }
        if (props.prodColumnSum > props.testColumnSum) {
            diff = `+${props.prodColumnSum - props.testColumnSum}`
        }
        if (props.prodColumnSum === props.testColumnSum) {
            diff = '0';
        }
    }

    return <svg x={`${props.x}%`} y={`${props.y}`} width="48" height="24" viewBox="0 0 48 24" fill="none"
                xmlns="http://www.w3.org/2000/svg">
        <g>
            {+diff < 0 && <rect width="48" height="24" rx="12" fill="#FC440F"/>}
            {+diff > 0 && <rect width="48" height="24" rx="12" fill="#00CC99"/>}
            {+diff === 0 && <rect width="48" height="24" rx="12" fill="#898290"/>}
            <text x={+diff === 0 ? "20" : '19'} y="16" textAnchor={'center'} fontFamily={'Roboto'} fontWeight={'bold'}
                  fontSize="14" fill="white">{diff}</text>
            <svg x={'10%'} y={'27%'} width="16" height="11" viewBox="0 0 8 10" fill="none"
                 xmlns="http://www.w3.org/2000/svg">
                {
                    +diff < 0 && <path
                        d="M4.00247 10L4.09239 10C4.18829 9.99542 4.2828 9.97515 4.37216 9.94C4.44068 9.91326 4.50471 9.87616 4.562 9.83C4.6076 9.80348 4.65104 9.77338 4.69189 9.74L7.6894 6.88C7.78452 6.78939 7.86089 6.68091 7.91413 6.56076C7.96737 6.44061 7.99645 6.31114 7.99969 6.17975C8.00625 5.91438 7.90721 5.65728 7.72437 5.465C7.54152 5.27272 7.28984 5.16101 7.0247 5.15444C6.75955 5.14788 6.50267 5.247 6.31054 5.43L5.00163 6.66L5.00163 1C5.00163 0.734783 4.89636 0.48043 4.70898 0.292893C4.5216 0.105357 4.26746 -1.63154e-07 4.00247 -1.74738e-07C3.73747 -1.86321e-07 3.48333 0.105357 3.29595 0.292893C3.10857 0.48043 3.0033 0.734783 3.0033 1L3.0033 6.59L1.71437 5.29C1.62149 5.19627 1.51098 5.12188 1.38922 5.07111C1.26746 5.02034 1.13686 4.9942 1.00496 4.9942C0.873059 4.9942 0.742462 5.02034 0.620704 5.07111C0.498946 5.12188 0.388438 5.19627 0.295552 5.29C0.201901 5.38296 0.127569 5.49356 0.0768425 5.61542C0.0261161 5.73728 2.20339e-07 5.86799 2.14569e-07 6C2.08798e-07 6.13201 0.0261161 6.26272 0.0768425 6.38458C0.127569 6.50644 0.201901 6.61704 0.295552 6.71L3.29306 9.71C3.38513 9.79995 3.4937 9.87126 3.61279 9.92C3.73601 9.97254 3.86853 9.99974 4.00247 10Z"
                        fill="white"/>
                }
                {
                    +diff > 0 && <path
                        d="M4.00247 10L4.09239 10C4.18829 9.99542 4.2828 9.97515 4.37216 9.94C4.44068 9.91326 4.50471 9.87616 4.562 9.83C4.6076 9.80348 4.65104 9.77338 4.69189 9.74L7.6894 6.88C7.78452 6.78939 7.86089 6.68091 7.91413 6.56076C7.96737 6.44061 7.99645 6.31114 7.99969 6.17975C8.00625 5.91438 7.90721 5.65728 7.72437 5.465C7.54152 5.27272 7.28984 5.16101 7.0247 5.15444C6.75955 5.14788 6.50267 5.247 6.31054 5.43L5.00163 6.66L5.00163 1C5.00163 0.734783 4.89636 0.48043 4.70898 0.292893C4.5216 0.105357 4.26746 -1.63154e-07 4.00247 -1.74738e-07C3.73747 -1.86321e-07 3.48333 0.105357 3.29595 0.292893C3.10857 0.48043 3.0033 0.734783 3.0033 1L3.0033 6.59L1.71437 5.29C1.62149 5.19627 1.51098 5.12188 1.38922 5.07111C1.26746 5.02034 1.13686 4.9942 1.00496 4.9942C0.873059 4.9942 0.742462 5.02034 0.620704 5.07111C0.498946 5.12188 0.388438 5.19627 0.295552 5.29C0.201901 5.38296 0.127569 5.49356 0.0768425 5.61542C0.0261161 5.73728 2.20339e-07 5.86799 2.14569e-07 6C2.08798e-07 6.13201 0.0261161 6.26272 0.0768425 6.38458C0.127569 6.50644 0.201901 6.61704 0.295552 6.71L3.29306 9.71C3.38513 9.79995 3.4937 9.87126 3.61279 9.92C3.73601 9.97254 3.86853 9.99974 4.00247 10Z"
                        fill="white"/>
                }
            </svg>
        </g>
    </svg>
};
